<template>
  <div>
    <p style="text-align: left;">
    <el-button  type="primary" @click="dialogFormAdd = true">添加</el-button>
    <el-button  type="danger" @click="DelEmpBtn" icon="el-icon-delete">批量删除</el-button>
    <el-button  type="primary" @click="addAll" icon="el-icon-circle-plus">一键添加</el-button>
    <el-button  type="primary" @click="addSelect" icon="el-icon-circle-plus">添加所选数据</el-button>
    <el-button  type="primary" @click="addTen" icon="el-icon-folder-add">每十条数据添加</el-button>
    <el-button  type="danger" @click="DeleteAll" icon="el-icon-error">一键删除</el-button>
    <el-button  type="danger" @click="DeleteAll1" >111</el-button>
    <el-button  type="danger" @click="DeleteAll2" >222</el-button>
    <el-button  type="primary" @click="DeleteAll3" >注销</el-button>
    <el-popover
        ref="popover"
        placement="right"
        title="增删改查"
        width="200"
        trigger="click"
        content="可以添加,删除,修改以及批量删除。">
    </el-popover>
    <el-button type="primary" v-popover:popover>提示</el-button>
    </p>
    <el-form :model="form">
      <el-form-item label="上传文件" :label-width="formLabelWidth">
    <el-upload
      class="upload-demo"
      ref="upload"
      action="https://jsonplaceholder.typicode.com/posts/"
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :file-list="fileList"
      :auto-upload="false">
      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
    </el-upload>
    </el-form-item>
    </el-form>


   <el-form :inline="true" class="demo-form-inline">
    <el-form-item>
      <!-- @input注解 当输入内容时直接自动搜索,不用点击 -->
      <el-input
        @input="helSearch()"
        v-model="search"
        class="search_name"
        size="mini"
        placeholder="输入关键字查询" clearable>
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button
        type="text"
        @click="helSearch()"
        class="el-icon-search">查询
      </el-button>
    </el-form-item>
    <el-form-item>
      <el-button
        class="el-icon-refresh"
        type="text"
        @click="refreshData()">刷新
      </el-button>
    </el-form-item>
    </el-form>

    <el-table :data="tableData" stripe border style="width:100%"  tooltip-effect="dark" ref="multipleTable" highlight-current-row @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center">
      </el-table-column>
      <el-table-column prop="id" label="ID" align="center" min-width="120">
        <template slot-scope="scope">
          <span>{{ scope.row.id}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="age" label="年龄" align="center" min-width="100">
        <template slot-scope="scope">
          <span>{{ scope.row.age}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="姓名" align="center" min-width="120">
        <template slot-scope="scope">
          <span>{{ scope.row.name}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" min-width="100">
        <template slot-scope="scope">
          <el-button type="warning" @click="toEdit(scope)">修改</el-button>
          <el-button type="danger" @click="deleteUser(scope)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="修改人员" :visible.sync="dialogFormEdit">
      <el-form :model="person" :rules="rules" ref="person" label-width="70px" class="demo-ruleForm" size="medium">
        <el-form-item label="年龄" prop="age">
          <el-input v-model="person.age" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="person.name" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEdit = false">取 消</el-button>
        <el-button type="primary" @click="edit(person)">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="添加人员" :append-to-body='true':visible.sync="dialogFormAdd">
      <el-form :model="person" :rules="rules" ref="person" label-width="70px" class="demo-ruleForm" size="medium">
<!--        <el-form-item label="编号" >
          <el-input v-model="person.id" auto-complete="one"></el-input> -->
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model="person.age" auto-complete="one"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="person.name" auto-complete="one"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormAdd = false">取 消</el-button>
        <el-button type="primary" @click="add(person)">确 定</el-button>
      </div>
    </el-dialog>
<br />
    <div class="block">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 30]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>

  </div>
</template>

<script>
  export default {
    name: 'PersonTable',
    data () {
      return {
        tableData: [],
        tableDatas: [],
        dialogFormEdit: false,
        dialogFormAdd:false,
        pageSize: 5,
        currentPage: 1,
        total: 0,
        search: '',
        person: {
          id: '',
          age: '',
          name: ''
        },
                 fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}]

         ,



        rules: {
            name: [
                { required: true, message: '请输入姓名', trigger: 'blur' },
                { min: 2, max: 7, message: '长度在 2 到 7 个字符', trigger: 'blur' }
            ],
            age: [
                { required: true, message: '请输入年龄', trigger: 'blur' },
                { min: 2, message: '2个字符以内', trigger: 'blur' }
            ],
        },

      }
    },
    methods: {
    messageFun: function(message, type){
      this.$message({
        message: message,
        type: type
      });
    },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      DelEmpById(scope) {
        //发送删除信息的请求
        this.$axios.delete("/del/" + scope,).then(result => {
          //提示信息
          this.$message({
            message: '删除成功！',
            type: 'success'
          });
          //重新加载数据
          this.init();
        });
      },

      // openFullScreen2() {
      //         const loading = this.$loading({
      //           lock: true,
      //           text: 'Loading',
      //           spinner: 'el-icon-loading',
      //           background: 'rgba(0, 0, 0, 0.7)'
      //         });
      //         setTimeout(() => {
      //           loading.close();
      //         }, 2000);

      //       },

      DelEmpBtn() {
        let selection = this.multipleSelection;
        let ids = '';
        //判断是否有职工信息被选中
        if (this.multipleSelection == undefined || this.multipleSelection.length == 0) {
          this.$message.error("未选中数据！");
        } else {
          for (let i = 0; i < selection.length; i++) {
            ids += selection[i].id + ",";
          }
          ids = ids.substring(0,ids.lastIndexOf(","));
            this.$confirm("确认删除id为"+ids+"的员工吗？", '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
             }).then(() => {
              this.DelEmpById(ids);
            }).catch(() => {
              this.messageFun('已取消删除', 'warning');
            });
        }
      },

      addAll() {
            this.$confirm("确认随机添加18条数据吗？", '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
             }).then(() => {
              //发送删除信息的请求
              this.$axios.post("/person/aa").then(result => {
                //提示信息
                this.$message({
                  message: '添加成功！',
                  type: 'success'
                });
                //重新加载数据
                this.init();
              });
            }).catch(() => {
              this.messageFun('已取消添加', 'warning');
            });

      },


      DeleteAll1() {
            this.$router.push({path: '/one'});
      },

      DeleteAll2() {
            this.$router.push({path: '/login'});
      },

      DeleteAll3() {
            this.$axios.post("/logout").then(result => {
              //提示信息
              this.$message({
                message: result.data.msg,
                type: 'success'
              });
              //重新加载数据
              this.$router.push({path: '/'});
            });
      },

      DeleteAll() {
            this.$confirm("确认删除所有数据吗？", '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
             }).then(() => {
              //发送删除信息的请求
              this.$axios.post("/person/deleteAll").then(result => {
                //提示信息
                this.$message({
                  message: '删除成功！',
                  type: 'success'
                });
                //重新加载数据
                this.init();
              });
            }).catch(() => {
              this.messageFun('已取消删除！', 'warning');
            });

      },

      addSelect() {
        let selection = this.multipleSelection;
        let persons = JSON.stringify(selection);
        const formData = new FormData()
        formData.append('persons', persons)
        let ids = '';
        //判断是否有职工信息被选中
        if (this.multipleSelection == undefined || this.multipleSelection.length == 0) {
          this.$message.error("未选中数据！");
        } else {
            this.$confirm("确认添加所选的数据吗？", '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
             }).then(() => {
               //发送删除信息的请求
                 this.$axios.post("/person/10",formData).then(result => {
                   //提示信息
                   this.$message({
                     message: '添加成功！',
                     type: 'success'
                   });
                   //重新加载数据
                   this.init();
                 });
               }).catch(() => {
                 this.messageFun('已取消添加！', 'warning');
               });
        }
      },

      addTen() {
            this.$confirm("确认每10条数据添加吗？", '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
             }).then(() => {
              //发送删除信息的请求
              this.$axios.post("/person/11").then(result => {
                let page = result.data;
                let persons = JSON.stringify(page);

                //提示信息
                this.$message({
                  message: '添加成功！数据为：'+persons,
                  type: 'success'
                });
                //重新加载数据
                this.init();
              });
            }).catch(() => {
              this.messageFun('已取消添加', 'warning');
            });

      },


      init () {
        this.onSearch1();
      },
      helSearch(){
        this.currentPage = 1;
        this.onSearch1();
      },
      onSearch1() {
          let postData = this.$qs.stringify({
          });
          this.$axios({
              method: 'post',
              url: '/person/pageHelp' ,
              params: {
                currPage:this.currentPage,
                pageSize:this.pageSize,
                onSearch:this.search
              }
          }).then(response =>
          {
              let page = response.data;
              this.tableData = page.data;
              this.total = page.total;

          }).catch(error =>
          {
              console.log(error);
          });
      },

      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.pageSize = val;
        this.onSearch1();
      },

      handleCurrentChange(val) {
        console.log(`第 ${val} 页`);
        this.currentPage = val;
        this.onSearch1();
      },

      add (person) {
        let params = new URLSearchParams()
        params.append('id', person.id)
        params.append('name', person.name)
        params.append('age', person.age)
        if(person.name !="" && person.age != ""){
          this.$axios.post('/insert',person ).then(res => {
            this.$message.success('添加成功')
            this.dialogFormAdd = false
            this.init()

             })
        }
        else {
            this.$message.error('姓名和年龄不能为空！')
           }
          // .catch(function (error) {
          //   console.log(error)
          // })
      },



      onSearch() {
          let postData = this.$qs.stringify({
              id: this.search
          });
          this.$axios({
              method: 'get',
              url: '/find' + "?id=" + this.search
          }).then(response =>
          {
              this.tableData = response.data;

          }).catch(error =>
          {
              console.log(error);
          });
      },
            refreshData() {
                location.reload();
            },


            // cancel() {
            //     this.dialogUpdate = false;
            //     this.dialogVisible = false;
            //     this.emptyUserData();
            // },
            // emptyUserData(){
            //     this.person = {
            //         id: '',
            //         name: '',
            //         age: ''
            //     }
            // },

      edit (person) {
        let params = new URLSearchParams()
        params.append('name', person.name)
        params.append('age', person.age)
        this.$axios.put('/update' , person).then(res => {
          // if (res.data.success === true) {
          this.$message.success('修改成功')
          this.dialogFormEdit = false
          this.init()
          // this.checkTable()
          // } else {
          //   this.$message.warning(res.data.msg)
          // }
        })
          .catch(function (error) {
            console.log(error)
          })
      },
      toEdit (scope) {
        this.person.id = scope.row.id
        this.person.age = scope.row.age
        this.person.name = scope.row.name
        this.dialogFormEdit = true
      },

      submitUpload() {
this.$refs.upload.submit();
            },
            handleRemove(file, fileList) {
              console.log(file, fileList);
            },
            handlePreview(file) {
              this.$message.error("选中数据！"+ file.file);
            },

            submits(file) { // 如果要自定义submit的话el-upload需要加上:http-request="submit"
              console.log(file)
              const form = new FormData()
              form.append('file', file.file)
              this.$axios({
                  method: 'post',
                  url: '/log/ftp',
                  contentType: 'multipart/form-data',
                  data:form
              }).then(response =>
          {
              let page = response.data;
              let persons = JSON.stringify(page);

              this.$message({
              						message: persons,
              						type: 'success'
              					});
          }).catch(error =>
          {
              console.log(error);
          });
            },


      deleteUser (scope) {
        if (!scope.row.id) {
          this.tableData.splice(scope.$index, 1)
        } else {
          this.$confirm('确认是否删除', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
          })
            .then(() => {
              console.log(scope.row.id)
              this.$axios.delete('/delete' + "?id=" + scope.row.id, {id: scope.row.id}).then(res => {
                // if (res.data.success === true) {
                this.$message.success('删除成功')
                this.init()
                // this.checkTable()
                // } else {
                //   this.$message.warning(res.data.msg)
                // }
              })
                .catch(function (error) {
                  console.log(error)
                })
            })
            .catch(() => {
              this.$message({
                type: 'warning',
                message: '已取消删除'
              })
            })
        }
      }
    },
    mounted: function () {
      this.init()
    }
  }
  /* eslint-disable no-new */

</script>

<style scoped>
  .search_name{
    width: 200px;
  },
  .pages{
  }
  body {
      margin: 0;
    }
</style>



