<template>
    <div class="login-container">
        <el-form :model="loginForm" :rules="rules2"
         status-icon
         ref="loginForm"
         label-position="left"
         label-width="0px"
         class="demo-ruleForm login-page">
            <h3 class="title">用户注册</h3>
            <el-form-item prop="username">
                <el-input type="text"
                    v-model="loginForm.userName"
                    auto-complete="off"
                    placeholder="用户名"
                ></el-input>
            </el-form-item>

                <el-form-item prop="password">
                    <el-input type="password"
                        v-model="loginForm.password"
                        auto-complete="off"
                        placeholder="密码"
                    ></el-input>
                </el-form-item>


            <el-form-item style="width:100%;">
                <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">
                  <span v-if="!loading">注 册</span>
                </el-button>

            </el-form-item>

        </el-form>
    </div>
</template>

<script>

import Cookies from "js-cookie";

export default {
    data(){
        return {

            codeUrl: "",
            cookiePassword: "",
            loginForm: {
              id: '',
              userName: "",
              password: "",
            },

            logining: false,
            ruleForm2: {
                userName: 'admin',
                password: '123456',
            },
            rules2: {
                userName: [{required: true, message: 'please enter your account', trigger: 'blur'}],
                password: [{required: true, message: 'enter your password', trigger: 'blur'}],
                code: [{ required: true, trigger: "change", message: "验证码不能为空" }]
            },
            checked: false
        }
    },

    methods: {


       handleSubmit(event){
            this.$refs.loginForm.validate((valid) => {
                if(valid){
                    this.logining = true;
                    this.$axios.post("/resign", this.loginForm).then(res => {
                      let persons = res.data;
                      if(persons.code == '200'){
                             this.logining = false;
                             this.$message.success(persons.msg);
                             this.$router.push({path: '/'});
                      }else{
                          this.logining = false;
                          this.getCode();
                          this.$alert(persons.msg, 'info', {
                              confirmButtonText: 'ok'
                          })
                      }
                    });

                }else{
                    console.log('error submit!');
                    return false;
                }
            })
        }
    }
};
</script>

<style scoped >

  .login {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-image: url("../assets/images/login-background.jpg");
    background-size: cover;
  }

.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
    width: 100%;
    height: 100%;
    background-image: url("../assets/images/login-background.jpg");
    background-size: cover;
}
.login-page {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
}


.login-code {
  width: 33%;
  height: 38px;
  float: right;


}
.el-login-footer {
  height: 40px;
  line-height: 40px;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: #fff;
  font-family: Arial;
  font-size: 12px;
  letter-spacing: 1px;
}
.login-code-img {
  height: 38px;

}



label.el-checkbox.rememberme {
    margin: 0px 0px 15px;
    text-align: left;

}
</style>



