<template>
    <div class="login-container">
        <el-form :model="loginForm" :rules="rules2"
         status-icon
         ref="loginForm"
         label-position="left"
         label-width="0px"
         class="demo-ruleForm login-page">
            <h3 class="title">系统登录</h3>
            <el-form-item prop="username">
                <el-input type="text"
                    v-model="loginForm.username"
                    auto-complete="off"
                    placeholder="用户名"
                ></el-input>
            </el-form-item>

                <el-form-item prop="password">
                    <el-input type="password"
                        v-model="loginForm.password"
                        auto-complete="off"
                        placeholder="密码"
                    ></el-input>
                </el-form-item>

            <el-form-item prop="code">
              <el-input
                v-model="loginForm.code"
                auto-complete="off"
                placeholder="验证码"
                style="width: 63%"
                @keyup.enter.native="handleLogin"
              >
              </el-input>
              <div class="login-code">
              <img :src="codeUrl" @click="getCode" class="login-code-img"/>
              </div>
            </el-form-item>


            <el-checkbox
                v-model="loginForm.rememberMe"
                class="rememberme"
            >记住密码</el-checkbox>

            <el-row style="text-align: center; margin-top: -10px;;">
                            <el-link type="primary">忘记密码</el-link>
                            <el-link type="primary" @click="doRegister()">用户注册</el-link>
                        </el-row>
            <el-form-item style="width:100%;">
                <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">
                  <span v-if="!loading">登 录</span>
                  <span v-else>登 录 中...</span>
                </el-button>

            </el-form-item>
        </el-form>
    </div>
</template>

<script>

import Cookies from "js-cookie";

export default {
    data(){
        return {

            codeUrl: "",
            cookiePassword: "",
            loginForm: {
              username: "",
              password: "",
              rememberMe:false,
              code: "",
              uuid: ""
            },

            logining: false,
            ruleForm2: {
                username: 'admin',
                password: '123456',
            },
            rules2: {
                username: [{required: true, message: 'please enter your account', trigger: 'blur'}],
                password: [{required: true, message: 'enter your password', trigger: 'blur'}],
                code: [{ required: true, trigger: "change", message: "验证码不能为空" }]
            },
            checked: false
        }
    },

    created() {
      this.getCode();
      this.getCookie();
    },

    methods: {

        getCode() {
          this.$axios({
              method: 'get',
              url: '/captchaImage',
          }).
          // this.$axios.get("/captchaImage").
          then(res => {
            let persons = res.data;
            this.codeUrl = "data:image/gif;base64," + persons.img;
            this.loginForm.uuid = persons.uuid;
          });
        },

        getCookie() {
          const username = Cookies.get("username");
          const password = Cookies.get("password");
          const rememberMe = Cookies.get('rememberMe')
          // const code = Cookies.get('code')
          this.loginForm = {
            username: username === undefined ? this.loginForm.username : username,
            password: password === undefined ? this.loginForm.password : password,
            rememberMe: rememberMe === undefined ? false : Boolean(rememberMe),
            // code: code === undefined ? this.loginForm.code : code
          };
        },

        doRegister() {
        this.$router.push({path: '/one'});
        },

        handleSubmit(event){
            this.$refs.loginForm.validate((valid) => {
              if (this.loginForm.rememberMe) {
                Cookies.set("username", this.loginForm.username, { expires: 30 });
                Cookies.set("password", this.loginForm.password, { expires: 30 });
                Cookies.set('rememberMe', this.loginForm.rememberMe, { expires: 30 });
                // Cookies.set('code', this.loginForm.code, { expires: 30 });
              } else {
                Cookies.remove("username");
                Cookies.remove("password");
                Cookies.remove('rememberMe');
                // Cookies.remove('code');
              }
                if(valid){
                    this.logining = true;
                    this.$axios.post("/login", this.loginForm).then(res => {
                      let persons = res.data;
                      if(persons.code == '200'){
                             this.logining = false;
                             this.$message.success(persons.msg);
                             sessionStorage.setItem('user', this.loginForm.username);
                             this.$router.push({path: '/file'});
                      }else{
                          this.logining = false;
                          this.getCode();
                          this.$alert(persons.msg, 'info', {
                              confirmButtonText: 'ok'
                          })
                      }
                    });

                }else{
                    console.log('error submit!');
                    return false;
                }
            })
        }
    }
};
</script>

<style scoped >

  .login {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-image: url("../assets/images/login-background.jpg");
    background-size: cover;
  }

.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
    width: 100%;
    height: 100%;
    background-image: url("../assets/images/login-background.jpg");
    background-size: cover;
}
.login-page {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
}


.login-code {
  width: 33%;
  height: 38px;
  float: right;


}
.el-login-footer {
  height: 40px;
  line-height: 40px;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: #fff;
  font-family: Arial;
  font-size: 12px;
  letter-spacing: 1px;
}
.login-code-img {
  height: 38px;

}



label.el-checkbox.rememberme {
    margin: 0px 0px 15px;
    text-align: left;

}
</style>



