import Vue from 'vue'
import Router from 'vue-router'
import file from '@/components/file'
import one from '@/components/one'
import login from '@/components/login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/file',
      name: 'file',
      component: file
    },
    {
      path: '/one',
      name: 'one',
      component: one
    },
    {
      path: '/',
      name: 'login',
      component: login
    }
  ]
})



